.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: calc(100vh - 128px);
}
.content {
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
}

.registrationForm{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.registrationTitle{
    font-family: Montserrat;
    color: #f5f5f5;
    font-size: 32px;
}
.registrationItem{
    display: flex;
    flex-direction: column;
    font-family: Montserrat;
    font-size: 16px;
    color: #f5f5f5;
}
.itemInput{
    margin-top: 5px;
    padding-left: 10px;
    width: 400px;
    height: 32px;
    background-color: #f5f5f5;
    border: none;
    font-family: Montserrat;
    font-size: 18px;
}
.itemError{
    font-family: Montserrat;
    font-size: 12px;
    color: #b42121;
    margin-top: 5px;
}
.loginButton{
    width: 120px;
    height: 33px;
    background-color: transparent;
    color: #f5f5f5;
    border: solid;
    border-color: #f5f5f5;
    border-width: 1px;
    font-family: Montserrat;
    font-size: 16px;
    font-weight: 400;
}
.loginButton:hover{
    border: none;
    background-color: #f5f5f5;
    color: #212528;
    cursor: pointer;
}
.registrationButtons{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin-top: 20px;
}
.buttonRegistration{
    height: 31px;
    background-color: transparent;
    color: #f5f5f5;
    font-family: Montserrat;
    font-size: 16px;
    margin-right: 20px;
    border: none;
    font-weight: 400;
}
.buttonRegistration:hover{
    font-weight: 600;
    cursor: pointer;
}
.showPas{
    font-size: 13px;
    font-weight: 300;
    margin-left: 2px;
}
.passwordShow{
    display: flex;
    flex-direction: row;
    margin-top: 2px;
}
.checkbox[type='checkbox']:checked{
    accent-color: #212528;
}

.checkbox:hover {
    cursor: pointer;
}